import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Rating from "@mui/material/Rating";

const dispatchedEvents = [];

const SrsMuiRating = function (props, ref) {
  return <Rating {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiRating component */
`;

const props = [
  {
    name: "name",
    defaultValue: "",
    type: "string",
  },
  {
    name: "value",
    defaultValue: null,
    type: "number",
  },
  {
    name: "precision",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Rating if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiRating),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
